<template>
  <div>
    <LayoutBox one="发布通知">
      <el-form ref="form" :model="form" :rules="rules" size="small" label-width="80px">
        <el-form-item label="发送用户" prop="type">
          <el-radio-group v-model="form.type">
            <el-radio :label="0">所有用户</el-radio>
            <el-radio :label="1">商家</el-radio>
            <el-radio :label="2">小超</el-radio>
            <el-radio :label="3">区级代理</el-radio>
            <el-radio :label="4">普通用户</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input v-model="form.title" style="width: 300px"></el-input>
        </el-form-item>
        <el-form-item label="通知类型" prop="contextType">
          <el-radio-group v-model="form.contextType" @change="onContextType">
            <el-radio :label="0">文字公告</el-radio>
            <el-radio :label="1">网址链接</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="内容" prop="context">
          <div class="context_box">
            <el-input v-model="form.context" class="context_input" :rows="5" type="textarea"></el-input>
            <div class="context_prompt">
              <span>文字公告：<span class="text">直接输入通知内容</span></span>
              <span>网址链接：<span class="text">请输入通知的链接地址</span></span>
            </div>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')">发送</el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  components: {
    LayoutBox
  },

  data () {
    return {
      form: {
        type: '',
        title: '',
        contextType: '',
        context: '',
      },
      rules: {
        type: [
          { required: true, message: '请选择发送用户', trigger: 'change' },
        ],
        title: [
          { required: true, message: '请选择输入标题', trigger: 'blur' },
        ],
        contextType: [
          { required: true, message: '请选择通知类型', trigger: 'change' },
        ],
        context: [
          { required: true, message: '请选择输入内容', trigger: 'blur' },
        ]
      }
    }
  },

  methods: {
    // 选择通知类型
    onContextType (e) {
      if (e === 1) {
        this.$message.error('该功能暂未开放')
        this.form.contextType = 0
      }
    },
    // 点击发送
    onSubmit (formName) {
      console.log(this.form)
      this.$refs[formName].validate((valid) => {
        if (valid) { this.apiAnnouncePush() }
      })
    },

    // api--------------------------------------------
    // 极光推送
    apiAnnouncePush () {
      let data = {
        ...this.form
      }
      this.$http.post('/woho/announce/push', data)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('发送成功')
            this.$router.push('/noticeList')
          } else { this.$message.error(res.data.msg) }
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .context_box{
    display: flex;
    justify-content: flex-start;
    .context_input{
      width: 300px;
    }
    .context_prompt{
      margin-left: 30px;
      font-size: 10px;
      line-height: 20px;
      font-weight: 600;
      color: #999999;
      display: flex;
      flex-direction: column;
      .text{
        font-weight: 500;
        color: rgba(66, 65, 65, 0.4);
      }
    }
  }
</style>
